import fetchWeather from "./fetchWeather";
import getGeoPosition from './getGeoPosition';
import PNotify from '../../node_modules/pnotify/dist/es/PNotify.js';
import weatherTemplate from "../templates/weather-info-template.hbs";

const refs = {
    searchForm: document.querySelector('#search-form'),
    weatherSection: document.querySelector('#weather'),
};

// WEATHER BY GEOLOCATION 

getGeoPosition()
    .then ( location => {
        fetchWeather(`${location.coords.latitude},${location.coords.longitude}`)
            .then( data => {
                createWeatherInfo(data);
            });
    })
    .catch( error => {
        PNotify.error({
            text: "Нет прав доступа к геопозиции, используйте поиск по имени города.",
            delay: 3000,
            width: '320px',
            icons: 'fontawesome4',
            addClass: 'geoposition-notify'
        });
    });

// WEATHER ON CLICK

function handleSubmit(e) {
    e.preventDefault();
    refs.weatherSection.innerHTML = "";

    const form = e.currentTarget;
    const inputValue = form.elements.city.value;

    fetchWeather(inputValue)
        .then( data => {
            createWeatherInfo(data);
        })
        .catch( error => {
            PNotify.error({
                text: `${error}`,
                delay: 5000,
                width: '320px',
                icons: 'fontawesome4',
                addClass: 'geoposition-notify'
            });
        });
}

refs.searchForm.addEventListener('submit', handleSubmit);

function createWeatherInfo (data) {
    refs.weatherSection.classList.remove('is-hidden');
    const markup = weatherTemplate(data);
    refs.weatherSection.insertAdjacentHTML('afterbegin', markup);    
};

