export default function (value) {
    const base = 'https://api.apixu.com/v1';
    const currentWeather = '/current.json';
    const reqParam = `?key=4839933a67a54cfd820163420190707&q=${value}&lang=ru`;

    return fetch(base+currentWeather+reqParam)
        .then( response => {
            if(response.ok) {
                return response.json();
            } else {
                throw new Error("Название города введено не корректно. Проверьте правильность ввода.");
            };
        })
};